  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("./neon.jpg");
  background-size: fill;
}
*/

#selectlevel {
  margin-top: 70px;
}

* {
  text-align: center;
  color: white;
  font-family: 'Bahnschrift'
}

#play {
  color: rgb(94, 93, 93);
  margin-top: 0px;
  margin-bottom: 50px;
  background-color: rgb(238, 229, 234);
  border: white 1px;
}
