input {
  size: 20px;
  font-size: 20px;
  background-color: rgb(238, 229, 234);
  color: rgb(128, 128, 128);
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  position: relative;
  top: 0px;
  border: 1px gray;
}

.match, .result {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 250px;
  font-size: 20px;
}

.match {
  text-decoration: none;
  background-color: #f3f5f3; /* Green */
  border: none;
  padding: 0px;
  cursor: pointer;
  color: rgb(128, 128, 128);
}

a {
  cursor: pointer;
  text-decoration: underline;
  margin: 8px;
}

.home {
  position: absolute;
  top: 10px;
  left: 16px;
  font-size: 18px;
  color: rgb(94, 93, 93)
}
#playagain {
  font-size: 20px;
  color:rgb(116, 114, 114);
}

#playagain:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    background-color: white;
}

#gameover {
  margin-top: 100px;
  font-size: 50px;
}
.quit {
  position: absolute;
  top: 10px;
  right: 16px;
  font-size: 18px;
  color: rgb(94, 93, 93)
}

.bartitle {
  position: block;
  margin-left: auto;
  margin-top: 0px;
  padding-top: 0px;
  margin-right: auto;
  text-align: center;
  top: 0px;
  color:rgb(223, 219, 219);
  font-size: 27px;
}

.guess {
  font-size: 40px;
  margin-top: 10px
}
.instruct {
  font-size: 25px;
  margin-bottom: 4px;
}



img {
  margin-top: 10px;
  height: 280px
}

span {
  font-size: 23px;
}

.getrelated {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  margin-top: 15px;
  font-size: 20px;
  color:rgb(94, 93, 93);
  margin-bottom: 30px;
}

.data, span {
  margin-bottom: 5px;
  margin-top: 0px;
}


.ReactPlayer {
  visibility: hidden
}

#bar {
  background-color: rgba(206, 172, 192, 0.973);
  color:rgb(135, 28, 145);
  padding-top: 10px;
  height: 37px;
  position: sticky;
}


option {
  color: black
}

.play {
  margin-top: 60px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-size: 20px;
  color: rgb(94, 93, 93)
}

iframe {
  margin-top: 10px;
}

select {
  font-size: 20px;
  margin-bottom: 20px;
  color: black;
}

.nextin {
  margin-top: 40px;
}

.question, .score {
  display: inline-block;
}

.question {
 margin-top: 90px;
 margin-right: 200px;
 margin-bottom: 0px;
}